import { Component } from "@angular/core";

@Component({
  selector: "app-home",
  templateUrl: "home.page.html",
  styleUrls: ["home.page.scss"]
})
export class HomePage {
  constructor() {}
  readyIn = true;
  countValue = "0";
  value = "0";
  secondValue = "0";
  operator = "+";
  numbers = [
    ["C", "L/D", "<-", "√"],
    [7, 8, 9, "x"],
    [4, 5, 6, "-"],
    [1, 2, 3, "+"],
    [0, ".", "/", "="]
  ];
  buttonPress(num) {
    if (!isNaN(num)) {
      if (this.readyIn) this.value = "" + num;
      else this.value += "" + num;
      this.readyIn = false;
    } else if (num === "C") {
      this.value = "0";
      this.readyIn = true;
    } else if (num === "=") {
      if (this.operator === "x")
        this.value = "" + parseInt(this.secondValue) * parseInt(this.value);
      else if (this.operator === "+")
        this.value = "" + (parseInt(this.value) + parseInt(this.secondValue));
      else if (this.operator === "/")
        this.value = "" + parseInt(this.secondValue) / parseInt(this.value);
      else if (this.operator === "√")
        this.value = "" + Math.sqrt(parseInt(this.secondValue));
      else if (this.operator === "-")
        this.value = "" + (parseInt(this.secondValue) - parseInt(this.value));
    } else {
      this.readyIn = true;
      this.secondValue = this.value;
      this.operator = num;
    }
    console.log(num);
  }
}
